import MasterPage from "../componentes/Master";
import fetch from "isomorphic-unfetch";

const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del BitCoin</h2>
                {console.log(props.precioBitcoin)}
            </div>

            <div className="col-md-8">
                <h2>Noticias sobre BitCoin</h2>
            </div>
            <div className="col-md-4">
                <h2>Pr√≥ximos Eventos BitCoin</h2>
            </div>
        </div>
    </MasterPage>
);

Index.getInitialProps = async () => {
    const precio = await fetch('https://min-api.cryptocompare.com/data/pricemultifull?fsyms=BTC&tsyms=DOP');
    const jsonPrecio = await precio.json();
    const resPrecio = await jsonPrecio.DISPLAY['BTC']['DOP'];

    

    return {
        precioBitcoin: resPrecio
    }
}

export default Index;
